<template>
    <section>
        <transition-group name="list" tag="ul"> <!-- ul  ul  -->
            <!-- li v-for="todoItem in todoItems" class="shadow"   
                : todoItem = text, index =  ,  :key=""(v-bind="key") -->
            <!-- li v-for="(todoItem, index) in todoItems" :key="todoItem" class="shadow" -->
            <li v-for="(todoItem, index) in propsdata" :key="todoItem" class="shadow"> <!-- v-for   propsdata , 
                                                                                        App todoItems    -->
                <i class="checkBtn fas fa-check" aria-hidden="true"></i>
                {{ todoItem }}
                <span class="removeBtn" type="button" @click="removeTodo(todoItem, index)">
                    <i class="far fa-trash-alt" aria-hidden="true"></i>
                </span>
            </li>
        </transition-group>
    </section>
</template>

<script>
export default{
    props : ["propsdata"],
    //   app    data 
    //local storage data -> vue data 
    /* data(){
        return{
            todoItems : [] //todoItems  : local storage    
                //    = li  v-for   
        }
    }, */
    //app  
    /* created(){ //       
        if(localStorage.length > 0){//   0  =     
            for(var i=0;i<localStorage.length;i++){ //
                this.todoItems.push(localStorage.key(i)); //  i key  todoItems      
                    //localStorage.key(i) = localStorage i key 
                    //push(localStorage.key(i)) = i key     
            }
        }
    }, */
    methods : {
        removeTodo(todoItem, index){
            //console.log(todoItem, index);
            localStorage.removeItem(todoItem);
            //this.todoItems.splice(index, 1) //splice() = ( index)    1(1) 
            this.$emit("removeTodo", todoItem, index);
        }
    }
}
</script>

<style scoped>
    section {}
    section ul {list-style:none;padding-left:0px;margin-top:0;text-align:center;}
    section .list-enter-active, section .list-leave-active {transition:all 1s;}
    section .list-enter, section .list-leave-to {opacity:0;transform:translateY(30px);}
    section ul li {display:flex;min-height:50px;height:50px;line-height:50px;margin:.5rem 0;padding:0 .9rem;background:#fff;border-radius:5px;}
    section ul li .checkBtn {line-height:45px;color:#dbd;margin-right:5px;}
    section ul li .removeBtn {margin-left:auto;color:#bbd;}
</style>


