<template>
  <div id="app">
    <TodoHeader></TodoHeader>
    <TodoInput v-on:addTodo="addTodo"></TodoInput> <!--    v-on   -->
    <TodoList v-bind:propsdata="todoItems" @removeTodo="removeTodo"></TodoList> 
    <TodoFooter v-on:removeAll="clearAll"></TodoFooter>
  </div>
</template>

<script>
  import TodoHeader from "./components/TodoHeader.vue"
  import TodoInput from "./components/TodoInput.vue"
  import TodoList from "./components/TodoList.vue"
  import TodoFooter from "./components/TodoFooter.vue"

  export default {
    data(){
      return {
        todoItems : [] //       
      }
    },
    methods : {
      addTodo(todoItem){ // todoItem = TodoInput     
        //       
        localStorage.setItem(todoItem, todoItem); //    
        this.todoItems.push(todoItem); //app  
      },
      clearAll(){
        localStorage.clear(); //  
        this.todoItems = []; // todoItem 
      },
      removeTodo(todoItem, index){
        localStorage.removeItem(todoItem);
        this.todoItems.splice(index, 1);
      }
    },
    created(){ //       
        if(localStorage.length > 0){//   0  =     
            for(var i=0;i<localStorage.length;i++){ //
                this.todoItems.push(localStorage.key(i)); //  i key  todoItems      
                    //localStorage.key(i) = localStorage i key 
                    //push(localStorage.key(i)) = i key     
            }
        }
    },
    components : {
      "TodoHeader" : TodoHeader,
      "TodoInput" : TodoInput,
      "TodoList" : TodoList,
      "TodoFooter" : TodoFooter
    }
  }
</script>

<style>
  body {text-align:center;background:#f6f6f8;}
  input {border-style:groove;width:200px;}
  button {border-style:groove;}
  .shadow {box-shadow:5px 10px 10px rgba(0, 0, 0, .03)};
</style>



