<template>
    <div class="inputBox shadow">
        <input type="text" v-model="newTodoItem" placeholder="Type what you have to do"
        v-on:keyup.enter="addTodo"> <!-- keyup.enter =   addTodo  -->
        <span class="addContainer" v-on:click="addTodo">
            <i class="addBtn fas fa-plus" aria-hidden="true"></i> <!-- aria-hidden="true" =       -->
        </span>

        <modal v-if="showModal" @close="showModal=false">
            <h3 slot="header"></h3> <!--   -->
            <span slot="footer" @click="showModal=false">  . <!--   -->
                <i class="closeModalBtn fas fa-times" aria-hidden="true"></i>
            </span>
        </modal>
    </div>
</template>

<script>
import Modal from "./common/Modal.vue"

export default{
    props : ["propsdata"],
    data() { // data : function(){} | vue   return     data 
        return{
            newTodoItem : "",
            showModal : false //    
        }
    },
    methods : {
        addTodo(){
            //console.log(this.newTodoItem);
            // 
            if(this.newTodoItem !== ""){ //  newTodoItem   =    
                //  &&   
                const value = this.newTodoItem && this.newTodoItem.trim(); //trim() :  
                // 
                //localStorage.setItem(value, value);   
                this.$emit("addTodo", value);// value =    
                this.clearInput(); //clearInput() = input box text  
            } else{
                this.showModal = !this.showModal; //   
            }
        },
        clearInput(){
        this.newTodoItem = "";
        }
    },
    components : {
        Modal : Modal //  
    }
}
</script>

<style scoped>
    .inputBox {background:#fff;height:50px;line-height:50px;border-radius:5px;}
    .inputBox input{display:inline-block;border:none;}
    .inputBox input:focus{outline:none;border:none;}
    .inputBox span.addContainer {display:block;width:3rem;border-radius:0 5px 5px 0;float:right;background:linear-gradient(to right,#dbd,#bbd);}
    .inputBox .addContainer .addBtn {color:#fff;vertical-align:middle}

</style>


